@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
}
.App{
  padding: 0px 20px 20px;
}

.Header{
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Header__Info{
  height: 100%;
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 30px;
  align-content: center;
}

.Header__Logo{
  font-size: 25px;
  color: #7ebc44;
  font-weight: bold;
}

.Header__Tel{
  display: flex;
  justify-content: center;
  align-items: center;
}
.Header__Tel img{
  width: 20px;
  height: 20px;
  margin-right: 10px;
}
.Header__Tel p{
  font-size: 20px;
  color: #822e7c;
  font-weight: bold;
}

.Header__Form{
  width: 220px;
  height: 50px;
  position: relative;
}
.Header__Form input[type="search"]{
  border: 1px solid #d6d4d4;
  padding: 9px 40px 9px 12px;
  width: 100%;
  height: 100%;
  font-size: 16px;
}
.Header__Form input[type="image"]{
  position: absolute;
  width: 20px;
  height: 20px;
  top: 15px;
  right: 10px;
}
h1{
  display: inline-block;
  padding: 10px 0;
  color: #822e7c;
  border-bottom: 3px solid #7ebc44;
  margin-bottom: 20px;
}
.Container{
  display: grid;
  width: 100%;
  height: auto;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(220px, 270px));
  grid-template-rows: repeat(auto-fill, minmax(350px, 350px));
}

.Card{
  padding: 10px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr auto;
  grid-gap: 10px;
  border: 1px solid #d6d4d4;
}
.Card__Img{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Card__Img img{
  width: 210px;
}
.Card__Body{
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto auto;
  grid-gap: 5px;
}
.Card__Price{
  color: rgb(126, 188, 68);
  font-size: 19px;
  font-weight: bold;
}
.Card__Description{
  color: rgb(105, 73, 177);
  font-size: 14px;
  font-weight: bold;
}
.Card__Add{
  width: 100%;
  height: 40px;
  text-align: center;
  font-size: 14px;
  font-weight: bold;
  background-color: rgb(126, 188, 68);
  border: none;
  color: white;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
